import {Inject, Injectable, PLATFORM_ID} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs/Observable';
import {first, tap} from 'rxjs/operators';
import {of} from 'rxjs/observable/of';
import {isPlatformServer} from '@angular/common';
import {makeStateKey, TransferState} from '@angular/platform-browser';
import {ApiService} from '../_services/api.service';
import {DataService} from '../_services/data.service';


@Injectable()
export class SetupResolver implements Resolve<any> {

    constructor(
        private data: DataService,
        private api: ApiService,
        @Inject(PLATFORM_ID) private platformId,
        private transferState: TransferState
    ) {

    }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {

        // const courseId = route.params['id'];

        const COURSE_KEY = makeStateKey<any>('setup');

        if (this.transferState.hasKey(COURSE_KEY)) {

            const setup = this.transferState.get<any>(COURSE_KEY, null);

            this.transferState.remove(COURSE_KEY);

            return of(setup);
        } else {
            return this.api.getSetup()
                .pipe(
                    tap(setup => {

                        if (isPlatformServer(this.platformId)) {
                            this.transferState.set(COURSE_KEY, setup);
                        }
                        // this.data.sendSetup(setup);

                    })
                );

        }


    }

}
